@import url(https://fonts.googleapis.com/css?family=Nunito+Sans);
:root {
  --dark-element: hsl(209, 23%, 22%);
  --dark-background: hsl(207, 26%, 17%);
  --light-text: hsl(200, 15%, 8%);
  --light-input: hsl(0, 0%, 52%);
  --light-background: hsl(0, 0%, 98%);
  --dark-text: white;
  --light-glow: hsl(0, 0%, 95%);
}

* {
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
}

ul {
  list-style: none;
}

.light-mode {
  background-color: hsl(0, 0%, 98%);
  background-color: var(--light-background);
}

.dark-mode {
  background-color: hsl(207, 26%, 17%);
  background-color: var(--dark-background);
}

.dark-mode p, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4{
  color: white;
  color: var(--dark-text)
}

.container {
  margin: 40px 100px;
}

.button {
  border: none;
  background: none;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.light-mode .button{
  color: hsl(0, 0%, 52%);
  color: var(--light-input)
}

.dark-mode .button{
  color: white;
  color: var(--dark-text)
}

.light-mode .button{
  background-color: white;
  background-color: var(--dark-text);
}

.dark-mode .button{
  background-color: hsl(209, 23%, 22%);
  background-color: var(--dark-element);
}

.light-mode .button {
  box-shadow: 0 0 10px  hsl(0, 0%, 95%);
  box-shadow: 0 0 10px  var(--light-glow);
}

@media (max-width: 768px) {
  .container {
    margin: 40px 20px;
  }
}
.header {
    justify-content: space-between;
    padding: 0px 100px;
    display: flex;
    align-items: center;
}

.toggle {
    display: flex;
    flex-direction: row;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    cursor: pointer;
}

.light-mode .header {
    color: var(--light-text);
    background-color: var(--dark-text);
    border-bottom: 5px solid var(--light-glow);
}

.dark-mode .header {
    color: var(--dark-text);
    background-color: var(--dark-element);
    border-bottom: 5px solid var(--dark-element);
}

@media(max-width: 768px) {
    .header {
        padding: 20px 20px;
    }
    h1 {
        font-size: 18px;
    }
}
.search-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.light-mode .search input::-webkit-input-placeholder{
    color: var(--light-input)
}

.light-mode .search input:-ms-input-placeholder{
    color: var(--light-input)
}

.light-mode .search input::placeholder, .light-mode .search input, .light-mode .search-icon, .light-mode #item{
    color: var(--light-input)
}

.dark-mode .search input::-webkit-input-placeholder{
    color: var(--dark-text)
}

.dark-mode .search input:-ms-input-placeholder{
    color: var(--dark-text)
}

.dark-mode .search input::placeholder, .dark-mode .search input, .dark-mode .search-icon, .dark-mode #item{
    color: var(--dark-text)
}

.light-mode .search, .light-mode .search input, .light-mode #menu{
    background-color: var(--dark-text);
}

.dark-mode .search, .dark-mode .search input, .dark-mode #menu{
    background-color: var(--dark-element);
}

.light-mode .search, .light-mode #menu {
    box-shadow: 0 0 10px  var(--light-glow);
}

.search {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 30%;
    height: 50px; 
    border-radius: 5px;
}

.search-icon {
    margin: 0px 20px;
}

.search input {
    border: none;
    width: 80%;
}

.search input:focus {
    outline: none;
}

#dropdown {
    height: 50px;
    width: 200px;
    font-weight: 600;
    padding: 0px 30px;
}

#dropdown::after {
    display: inline-block;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
}

#menu {
    display: flex;
    flex-direction: column;
    width: 200px;
    border-radius: 5px;
    padding: 20px 0px;
}

#item {
    text-decoration: none;
    padding: 5px 20px;
}

#item:hover {
    border-radius: 5px;
}

.light-mode #item:hover {
    background-color: var(--light-background);
}

.dark-mode #item:hover {
    background-color: var(--dark-background);
}

@media(max-width: 768px) {
    .search {
        width: 100%
    }

    .search-bar {
        align-items: start;
        flex-direction: column;
        grid-gap: 40px;
        gap: 40px;
    }
}
.light-mode .card {
    background-color: var(--dark-text);
    box-shadow: 0 0 10px  var(--light-glow);
}

.dark-mode .card {
    background-color: var(--dark-element);
}

.card {
    width: 265px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    overflow: hidden;
    margin: 30px 0;
    cursor: pointer;
}

.card img {
    object-fit: cover;
    height: 170px;
}

.card-body {
    padding: 0px 30px 40px 30px;
}

.card-details li {
    display: flex;
    flex-direction: row;
    padding: 4px 0px;
}

.card-details, .card-details h4, .card-details p {
    padding: 0px;
    margin: 0px;
}
.card-list {
    margin: 0 100px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 256px);
    grid-gap: 40px;
    justify-content: space-between;
}

.back-button {
    height: 50px;
    width: 200px;
    padding-left: 65px;
    padding-right: 75px;
}

.info {
    margin: 70px 0px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 100px;
    gap: 100px;
}

.info img {
    width: 100%;
}

.details-page {
    display: flex;
    flex-direction: column;
}

.info h2 {
    font-size: 40px;
}

.details {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.details li {
    display: flex;
    flex-direction: row;
    padding: 7px 0px;
}

.details ul, .details h4, .details p {
    padding: 0px;
    margin: 0px;
}

.borders-button {
    height: 30px;
    width: 100px;
    justify-content: center
}

.borders {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
    padding: 20px 0;
    flex-wrap: wrap;
}

@media(max-width: 768px) {
    .info {
        grid-template-columns: 1fr;
        grid-gap: 20px;
        gap: 20px;
    }
}
